
.grid-container {
	display: inline-grid;
	grid-template-columns: auto auto;
	width: fit-content;
}

.grid-item {
	
	padding: 0.5em;
	margin-left:-1px;
	margin-bottom:-1px;
	padding-left: 10px;
	padding-right: 10px;
	
	border: 1px solid #AAA;
	background-color: #333;
	text-align: center;
	white-space: pre-wrap;
}

.cpu-delay-input {
	width: 3em;
	
	background: none;
	outline: none;
	border: none;
	border-bottom: 1px solid #BBB;
	
	color: #FFF;
	text-align: right;
	font-family: Consolas;
	font-size: 0.9rem;
	
	transition: all 50ms linear;
}
.cpu-delay-input:focus {
	border-bottom: 1px solid rgb(137, 226, 102);
}