html, body, #root, .container {
	width: 100%;
	height: 100%;
	margin: 0%;
	padding: 0%;
	
	min-width:1000px;
	
	overflow: hidden;
	scroll-behavior: smooth;
	
	font-size: 16px;
	font-weight: lighter;
	color: #FFF;
	font-family: Consolas, monaco, monospace;
}

.container {
	width: 96%;
	height: calc(100% - 4vw);
	padding: 2vw;
	
	background-color: #222222;
}