.panel {
	margin: 2vw;
	padding: 1vw;
	height: calc(100% - 6vw);
	
	float: left;
	
	background-color: #323232;
}

.header {
	padding: 1em;
	height: 1.2em;
	
	border: 1px solid #AAA;
	background-color: none;
	
	text-align: center;
	line-height: 1;
	font-size: 1rem;
}

.panelContainer {
	width: calc(100% - 2vw);
	height: calc(100% - 3em - 2vw);
	margin-top: 1vw;
	margin-bottom: 1vw;
	padding-left: 1vw;
	padding-right: 1vw;
	
	overflow-x: hidden;
	overflow-y: auto;
	scroll-behavior: smooth;
	
	/* background-color: #666; */
}

.panelContainer::-webkit-scrollbar {
	width: 6px;
	height: 6px;
	background: #333;
}

.panelContainer::-webkit-scrollbar-track {
	background: #333;
}

.panelContainer::-webkit-scrollbar-thumb {
	border: 2px solid #333;
	background-color: #AAA;
}

.PanelSelector {
	margin-top: -5px;
	
	outline: none;
	border: none;
	
	background: #333;
	color: #FFF;
	font-size: 1rem;
	font-family: Consolas, monospace;
}