.panel-container-row {
	width: 200%;
	margin-left: -50%;
	margin-top: 10px;
	margin-bottom: 25px;
	text-align: center;
	font-size: 0.9rem;
}

.panel-button {
	display: inline-block;
	/* width: 30%; */
	padding: 1em;
	padding-top: 0.6em;
	padding-bottom: 0.6em;
	
	margin-left: -1px;
	
	border: 1px solid rgb(137, 226, 102);
	color: rgb(137, 226, 102);
	text-align: center;
	
	transition: all 50ms linear;
	user-select: none;
}
.panel-button:hover {
	background: rgba(137, 226, 102, 0.37);
	text-shadow: 1px 1px #333;
}

.panel-button:active {
	background: rgba(137, 226, 102, 0.8);
	color: #FFF;
	text-shadow: 1px 1px #000;
}